.post-topic {
  margin: 0 4px;
}

body {
  font-family: 'Lato', sans-serif;
}

.app-wrapper {
  display: grid;
  justify-content: center;
}

.logo-main,
.recent-posts
{
  display: grid;
  justify-items: center;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}



@mixin default-page-layout {
  height: 100vh;
  width: 66vw;
  display: grid;
  align-content: space-around;
}

$color-gray-one: #666666;
$color-gray-two: #B3B3B3;
$color-gray-three: #d8d8d8;
$color-blue-one: #0079FF;

.no-content {
  color: $color-gray-one;
  font-size: 14px;
  line-height: 17px;
}

@import './mixins';
@import './searchBar';
@import './recentPosts';
@import './post';
@import './home';
@import './results';